
// components/GlobalBackground.tsx
"use client";
import React, { useState, useEffect } from 'react';

const GlobalBackground: React.FC = () => {
  // Track mouse position
  const [mouse, setMouse] = useState({ x: 0, y: 0 });

  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      setMouse({ x: e.clientX, y: e.clientY });
    };
    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  // Calculate parallax offset
  const getOffset = (elX: number, elY: number, intensity: number) => {
    const dx = (mouse.x - window.innerWidth / 2) / window.innerWidth;
    const dy = (mouse.y - window.innerHeight / 2) / window.innerHeight;
    return {
      x: -dx * intensity + elX,
      y: -dy * intensity + elY,
    };
  };

  return (
    <>
      {/* Background gradient */}
      <div className="absolute inset-0 -z-50 bg-gradient-to-br from-[#2a1155] via-[#180b36] to-[#3b0080]" />

      {/* Animated blobs */}
      {[...Array(20)].map((_, i) => {
        const size = Math.floor(200 + Math.random() * 200);
        const top = Math.random() * 100;
        const left = Math.random() * 100;
        const opacity = (0.4 + Math.random() * 0.3).toFixed(2);
        const delay = Math.random() * 4;
        const colors = [
          `rgba(138,43,226,${opacity})`,
          `rgba(186,85,211,${opacity})`,
          `rgba(153,50,204,${opacity})`,
          `rgba(255,105,180,${opacity})`,
          `rgba(147,112,219,${opacity})`,
          `rgba(72,61,139,${opacity})`,
        ];
        const bg = colors[Math.floor(Math.random() * colors.length)];

        return (
          <div
            key={i}
            className="absolute rounded-full filter blur-3xl animate-blob"
            style={{
              width: `${size}px`,
              height: `${size}px`,
              top: `${top}%`,
              left: `${left}%`,
              backgroundColor: bg,
              animationDelay: `${delay}s`,
            }}
          />
        );
      })}

      {/* Subtle grid overlay */}
      <div className="absolute inset-0 pointer-events-none bg-grid-black/[0.04]" />

      {/* Diagonal lines overlay */}
      <svg className="absolute inset-0 w-full h-full pointer-events-none" xmlns="http://www.w3.org/2000/svg">
        <defs>
          <pattern id="diagonalLines" patternUnits="userSpaceOnUse" width="40" height="40" patternTransform="rotate(45)">
            <line x1="0" y1="0" x2="0" y2="40" stroke="rgba(255,255,255,0.02)" strokeWidth="1" />
          </pattern>
        </defs>
        <rect width="100%" height="100%" fill="url(#diagonalLines)" />
      </svg>

      {/* Bokeh lights interactive */}
      <div className="absolute inset-0 pointer-events-none">
        {[...Array(25)].map((_, i) => {
          const size = Math.floor(6 + Math.random() * 10);
          const baseX = Math.random() * 100;
          const baseY = Math.random() * 100;
          const { x, y } = getOffset(baseX, baseY, 5); // intensity 5px
          const driftDelay = (Math.random() * 4).toFixed(2);
          const flickerDelay = (Math.random() * 2).toFixed(2);

          return (
            <span
              key={i}
              className="absolute rounded-full bg-white"
              style={{
                width: `${size}px`,
                height: `${size}px`,
                left: `${x}%`,
                top: `${y}%`,
                filter: 'blur(5px)',
                opacity: 0.1,
                animation: `drift 4s ease-in-out ${driftDelay}s infinite, flicker 2s ease-in-out ${flickerDelay}s infinite`,
              }}
            />
          );
        })}
      </div>
    </>
  );
};

export default GlobalBackground;
